.info-section {
  padding: 70px 10px 40px 10px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: var(--color-variant);
  flex-direction: row-reverse;
}
.info-container {
  width: 50%;
  /* height:400px; */
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}
.info-card {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 20px;
}
.info-img {
  width: 50%;
  object-fit: cover;
  height: 400px;
}
.info-list {
  align-items: center;
  justify-content: space-around;
  width: 80%;
  text-transform: capitalize;
}
.info-list h6{
  color: white;
}
.info-icon {
  font-size: 3rem;
  margin-right: 20px;
  color: var(--color-bold);
}
.info-card p {
  text-transform: lowercase;
  font-size: 1rem;
}
.info-card h6 {
  font-size: 1.2rem;
  text-transform: capitalize;
  margin: 10px 0px 10px 0px;
  width: fit-content;
  font-weight: 500;
}
.info-line {
  margin: 0%;
  height: 2px;
  margin-top: 5px;
  width: 100px;
}
.invest-now {
  margin-left: 10px;
  color: rgb(33, 151, 255);
  text-transform: uppercase;
}
@media (max-width: 990px) and (min-width: 768px) {
  .info-section {
    flex-direction: column-reverse;
  }
  .info-container {
    width: 90%;
  }
}
@media screen and (max-width: 767px) {
  .info-section {
    flex-direction: column-reverse;
  }
  .info-container {
    width: 100%;
  }
  .info-img {
    object-fit: contain;
    height: 300px;
    width: 100%;
    margin-bottom: 10px;
  }
  .info-card {
    width: 100%;
  }
}
