.more-info-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 50px 0px 50px 120px;
}
.more-info-card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.more-info-icon {
  font-size: 4rem;
  margin-bottom: 20px;
  color:white;
}
@media (max-width: 990px) and (min-width: 768px) {
  .more-info-section {
    padding: 60px 10px 40px 10px;
    height: auto;
  }
}
@media screen and (max-width: 767px) {
  .more-info-section {
    flex-direction: column;
    padding: 0px;
  }
  .more-info-card {
    margin-top: 20px;
  }
}
