header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px;
  background-color: transparent;
  position: relative;
  z-index: 2;
  background-color: var(--color-variant);
  position: sticky;
  top: 0;
  right: 0;
}
nav {
  width: 40%;
}
ul {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
li a {
  color: var(--color-bold);
}
li a:hover {
  color: var(--color-bold);
}
.get-started-btn {
  text-transform: capitalize;
}
.home-sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 0px;
  overflow-x: hidden;
  background-color: white;
  box-shadow: 3px 0px 10px #ddd;
}
.u {
  font-size: 3rem;
  font-weight: bold;
  color: var(--color-bold);
  text-transform: uppercase;
  margin-right: 5px;
}
.logo {
  color: var(--color-bold);
}
/* From cssbuttons.io by @adamgiebl */
.cssbuttons-io-button {
  background: var(--color-bold);
  color: white;
  font-family: inherit;
  padding: 0.35em;
  padding-left: 1.2em;
  font-size: 17px;
  font-weight: 500;
  border-radius: 0.9em;
  border: none;
  letter-spacing: 0.05em;
  display: flex;
  align-items: center;
  box-shadow: inset 0 0 1.6em -0.6em var(--color-bold);
  overflow: hidden;
  position: relative;
  height: 2.8em;
  padding-right: 3.3em;
}

.cssbuttons-io-button .icon {
  background: white;
  margin-left: 1em;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2.2em;
  width: 2.2em;
  border-radius: 0.7em;
  box-shadow: 0.1em 0.1em 0.6em 0.2em var(--color-bold);
  right: 0.3em;
  transition: all 0.3s;
  cursor: pointer;
}
.menu-container {
  cursor: pointer;
}
.cssbuttons-io-button:hover .icon {
  width: calc(100% - 0.6em);
}

.cssbuttons-io-button .icon svg {
  width: 1.1em;
  transition: transform 0.3s;
  color: var(--color-bold);
}
.menu {
  width: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  cursor: pointer;
  display: none;
}
.menu-line {
  width: 55%;
  height: 4px;
  background-color: black;
  border-radius: 2px;
  margin-top: 7px;
}
@media screen and (max-width: 767px) {
  .header-btn-container {
    display: none;
  }
  .cssbuttons-io-button {
    display: none;
  }
  header {
    justify-content: flex-end;
    padding-right: 13px;
  }
  .logo-container {
    justify-content: flex-end;
    margin-left: auto;
    align-self: flex-end;
  }
  nav {
    display: none;
  }
}
