.input-container {
  font-size: 1rem;
  width: 90%;
  position: relative;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 25px;
}
.login-form-container {
  width: 100%;
  padding: 20px 0px 20px 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
}
.input {
  width: 100%;
  height: 100%;
  padding-top: 25px;
  color: var(--color-bold);
  position: static;
  margin: auto;
  background-color: none;
  border-radius: 10px;
  font-size: 1rem;
}

label {
  position: absolute;
  bottom: 0px;
  right: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
  border-bottom: 2px solid rgb(232, 232, 232);
  overflow: hidden;
  color: rgb(190, 225, 255);
}
label::after {
  content: '';
  position: absolute;
  height: 100%;
  width: 100%;
  border-bottom: 2px solid var(--color-bold);
  right: 0px;
  bottom: 0px;
  z-index: 2;
  transform: translateX(100%);
  transition: 0.3s ease all;
  background-color: none;
}
.content-name {
  position: absolute;
  bottom: 5px;
  left: 0px;
  transition: all ease-in 0.1s;
  font-size: 1rem;
  text-transform: capitalize;
  color: rgb(180, 180, 180);
}
.input:focus + label .content-name,
.input:valid + label .content-name {
  transform: translateY(-100%);
  color: black;
}

.input:focus + label::after,
.input:valid + label::after {
  transform: translateX(0%);
}
.input:focus + label {
  border-bottom: none;
}
.login-form {
  width: 450px;
  padding: 100px 0px 50px 0px;
  box-shadow: 0px 0px 40px #ddd;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 10px;
}
.form-logo-container > svg {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 10px 10px 0px 0px;
  height: fit-content;
}
.login-icon {
  align-self: flex-end;
  font-size: 2rem;
  color: #ddd;
  position: static;
  margin-right: 10px;
}
.input-controller {
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  position: relative;
}
.invest-btn {
  text-decoration: none;
  position: absolute;
  border: none;
  font-size: 1.1rem;
  text-transform: capitalize;
  cursor: pointer;
  font-family: inherit;
  color: #fff;
  width: 80%;
  height: 3em;
  line-height: 2em;
  text-align: center;
  background: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
  background-size: 300%;
  border-radius: 30px;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
}

.invest-btn:hover {
  animation: ani 8s linear infinite;
  border: none;
}

@keyframes ani {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 400%;
  }
}

.invest-btn:before {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  z-index: -1;
  background: linear-gradient(90deg, #03a9f4, aliceblue, #03a9f4);
  background-size: 400%;
  border-radius: 35px;
  transition: 1s;
}
.invest-btn-container {
  display: flex;
  align-items: center;
  justify-items: center;
  width: 100%;
  position: relative;
  height: 50px;
  margin-top: 50px;
  margin-bottom: 30px;
}

.invest-btn:hover::before {
  filter: blur(20px);
}

.invest-btn:active {
  background: linear-gradient(32deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
}
.login-form p , .login-form a{
  color:var(--color-primary);
}
.login-form-container{
  min-height: 100vh;
}
@media screen and (max-width: 767px) {
  .login-form {
    width: 95%;
    box-shadow: none;
  }
  .input-container{
    width: 100%;
    background-color: transparent;
  }
}
