.add-icon {
  margin-left: 30px;
  font-size: 2rem;
  color: white;
  cursor: pointer;
}

.faq-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 20px 30px 20px;
  margin: auto;
  width: 80%;
  margin-top: 30px;
  background-color: rgba(255, 255, 255, 0.923);
}
.faq-card p {
  color: black;
  width: 90%;
}
.answer-container {
  width: 80%;
  padding: 30px 20px 30px 20px;
  background-color: rgb(250, 250, 2450);
  margin: auto;
  transition: all 0.3s ease;
  border-top: 1px solid #ddd;
}
.faq-landpage {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  padding: 10px 80px 30px 80px;
  background-color: white;
  margin: auto;
  width: 100%;
}
.faq-landpage-text-container {
  width: 50%;
}
.faq-landpage-text-container p{
  color:black;
}
.faq-landpage-text-container p::first-letter{
  text-transform: uppercase;
}
.answer-container p{
  color: black;
}
.faq-card p::first-letter{
  text-transform: uppercase;
}
.answer-container p::first-letter{
  text-transform: uppercase;
}
.faq-landpage-text-container h5{
  color:var(--color-variant);
} 
.img {
  width: 500px;
  object-fit: contain;
  height: 400px;
}
@media (max-width: 990px) and (min-width: 768px) {
  .faq-landpage {
    flex-direction: column;
  }
  .faq-landpage-text-container {
    width: 70%;
    margin-top: 40px;
  }
}
@media screen and (max-width: 767px) {
  .faq-landpage {
    flex-direction: column;
    padding: 0px;
  }
  .faq-landpage-text-container {
    width: 80%;
    margin-top: 40px;
  }
  .answer-container {
    width: 100%;
  }
  .faqs-container {
    padding: 0px;
    margin-bottom: 50px;
  }
  .faq-card {
    width: 100%;
  }
  .img{
    max-width: 90%;
  }
}
