footer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 30px 10px 10px 30px;
}
.contact-form {
  width: 550px;
  padding: 20px;
}
.footer-line-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 20px;
}
.contact-line {
  width: 50px;
  height: 2px;
  background-color: black;
}
.contact-input {
  width: 100%;
  height: 40px;
  background-color: #ddd;
  width: 60%;
  font-size: 1rem;
  text-indent: 12px;
}
.contact-header p {
  margin-bottom: 20px;
}
.contact-header {
  width: 100%;
}
.submit-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px;
  background-color: #ddd;
  border-radius: 16rem;
}
.footer-submit-btn {
  height: 40px;
  width: 40%;
  border-radius: 16rem;
  font-size: 1.2rem;
  text-transform: capitalize;
  background-color: var(--color-bold);
  color: white;
  cursor: pointer;
}
.footer-submit-btn:hover {
  background-color: var(--color-primary);
  color: rgb(23, 23, 23);
}
.quick-links-container {
  margin-top: -20px;
}
.quick-links {
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  width: 350px;
}
.quick-links li {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 20px;
}
.quick-link-icon {
  font-size: 1.8rem;
  margin-right: 10px;
}
@media screen and (max-width: 767px) {
  footer {
    flex-direction: column;
    padding: 0px;
    height: auto;
  }
  .quick-links-container{
    height: auto;
    margin: 0px;
    width: 100%;
  }
  .quick-links{
    height: auto;
    width: 90%;
    margin-top: 20px;
  }
  .quick-links li{
    width: 90%;
  }
  .quick-links li svg{
    color:white;
  }
  .contact-form {
    width: 100%;
  }
  .footer-line-container{
    width: 100%;
  }
  .footer-line-container h5{width: 100%;
  }
}
