.land-page {
  width: 100%;
}
.land-text {
  width: 55%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-bottom: 50px;
  height: 100%;
  flex-direction: column;
}
.land-text p {
  text-align: left;
  margin-bottom: 40px;
  width: 100%;
}
.land-text h5 {
  text-align: left;
  width: 100%;
  font-size: 2.5rem;
  font-weight: bold;
}
.land-text button {
  border: 2px solid rgb(59, 161, 251);
  background-color: transparent;
  font-size: 1.3rem;
  color: rgb(59, 161, 251);
  transition: all ease 0.3s;
  padding: 15px 50px;
  cursor: pointer;
  border-radius: 5px;
  align-self:flex-start;

}
.land-text button:hover {
  background-color: rgb(117, 191, 255);
  border: 2px solid rgb(117, 191, 255);
  color: white;
}
.slide-img-container {
  width: 40%;
  height: 400px;
  object-fit: cover;
}
.slider-img {
  height: 100%;
  width: 100%;
  object-fit: contain;
  filter: drop-shadow(10px 10px 10px rgb(79, 79, 79));
}
.header-btn-container {
  display: flex;
  width: 20%;
  align-items: center;
  justify-content: center;
  position: relative;
}

.swiper-container {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
  background-image: url(../../../public/bg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.slide {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 80%;
  height: 100%;
  cursor: pointer;
  padding: 60px 20px 60px 20px;
  background-color:  rgba(8, 1, 34, 0.81);
}
.nft {
  height: 400px;
}
.nft-n {
  text-transform: uppercase;
  color: rgb(216, 167, 19);
}
.nft-f {
  text-transform: uppercase;
  color: var(--color-bold);
  margin: 0px 2px 0px 2px;
}
.nft-t {
  text-transform: uppercase;
  color: rgb(164, 68, 202);
}
@media (max-width: 990px) and (min-width: 768px) {
  .slide {
    flex-direction: column-reverse;
    padding-top: 0px;
  }
  .land-text {
    width: 95%;
  }
  .forex {
    flex-direction: column;
  }
  .nft {
    height: 350px;
    margin-bottom: 20px;
  }
}
@media screen and (max-width: 767px) {
  .slide {
    flex-direction: column-reverse;
    padding-top: 0px;
  }
  .land-text {
    width: 100%;
    padding-top: 50px;
  }
  .forex {
    flex-direction: column;
  }
  .nft {
    height: 250px;
    margin-bottom: 20px;
  }
  .land-text h5{
    font-size: 1.8rem;
  }
  .land-text p{
    font-size: 1.1rem;
  }
}
