.invest-plan-section {
  padding: 100px 20px 40px 20px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: auto;
}
.invest-card {
  min-height: 400px;
  width: 300px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--color-variant);
  margin-top: 100px;
  flex-direction: column;
}
.invest-card-header {
  border: 5px dotted var(--color-variant);
  height: 150px;
  width: 150px;
  border-radius: 50%;
  background-color: var(--color-bold);
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: -20%;
  left: 50%;
  transform: translateX(-50%);
}
.invest-badge {
  text-transform: capitalize;
  color: white;
  opacity: 1;
}
.invest-card-text {
  margin-top: 100px;
  padding: 0px 20px 0px 20px;
  text-align: center;
}
.invest-card-text p::first-letter {
  text-transform: uppercase;
}
.prize-tag {
  font-weight: 600;
  margin: 15px 0px 20px 0px;
}
.invest-btn {
  text-decoration: none;
  position: absolute;
  border: none;
  font-size: 1.1rem;
  text-transform: capitalize;
  cursor: pointer;
  font-family: inherit;
  color: #fff;
  width: 80%;
  height: 3em;
  line-height: 2em;
  text-align: center;
  background: var(--color-primary);
  background-size: 300%;
  border-radius: 30px;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
}

.invest-btn:hover {
  animation: ani 8s linear infinite;
  border: none;
}

@keyframes ani {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 400%;
  }
}

.invest-btn:before {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  z-index: -1;
  background: linear-gradient(90deg, #03a9f4, aliceblue, #03a9f4);
  background-size: 400%;
  border-radius: 35px;
  transition: 1s;
}
.invest-btn-container {
  display: flex;
  align-items: center;
  justify-items: center;
  width: 100%;
  position: relative;
  height: 50px;
  margin-top: 30px;
}

button:hover::before {
  filter: blur(20px);
}

button:active {
  background: linear-gradient(32deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
}

@media (max-width: 990px) and (min-width: 768px) {
  .invest-plan-section {
    flex-wrap: wrap;
    height: auto;
  }
  .invest-card:last-child {
    margin-top: 110px;
  }
}
@media screen and (max-width: 767px) {
  .invest-plan-section {
    flex-direction: column;
    padding:20px 10px ;
  }
  .invest-card {
    width: 95%;
    height: 400px;
  }
  .invest-plan-section {
    padding: 100px 20px 40px 20px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: auto;
  }
}
