.contact-landpage {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: white;
  position: relative;
}
.contact-img {
  margin-top: 70px;
}
.cont-form {
  width: 50%;
  margin-top: 70px;
}
.contact-text-area {
  min-height: 100px;
  width: 90%;
  font-size: 1rem;
  border-bottom: 1px solid #ddd;
}
.contact-text-area::placeholder {
  font-size: 1rem;
  text-transform: capitalize;
  color: rgb(161, 161, 161);
}
.textarea-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 50px;
  width: 90%;
}
.textarea-icon {
  align-self: flex-start;
  margin-right: 0px;
}
.contact-form-btn-container {
  margin-top: 20px;
  width: 80%;
  margin-left: 60px;
}
.contact-form-btn {
  width: 100%;
  font-size: 1rem;
  background: var(--color-bold);
  color: white;
  padding: 0.7em 1em;
  padding-left: 0.9em;
  display: flex;
  align-items: center;
  border: none;
  overflow: hidden;
  transition: all 0.2s;
}

.contact-form-btn span {
  display: block;
  margin-left: 0.3em;
  transition: all 0.3s ease-in-out;
}

.contact-form-btn svg {
  display: block;
  transform-origin: center center;
  transition: transform 0.3s ease-in-out;
}

.contact-form-btn:hover .svg-wrapper {
  animation: fly-1 0.6s ease-in-out infinite alternate;
}

.contact-form-btn:hover svg {
  transform: translateX(1.2em) rotate(45deg) scale(1.1);
}

.contact-form-btn:hover span {
  transform: translateX(5em);
}

.contact-form-btn:active {
  transform: scale(0.95);
}
.contact-form-header {
  text-align: center;
  position: absolute;
  top: 0;
  margin-top: 40px;
  color: rgba(0, 0, 0, 0.658);
  width: fit-content;
}
.contact-form-line {
  margin: auto;
  width: 50%;
  background-color: var(--color-bold);
}
@keyframes fly-1 {
  from {
    transform: translateY(0.1em);
  }

  to {
    transform: translateY(-0.1em);
  }
}
@media (max-width: 990px) and (min-width: 768px) {
  .contact-landpage {
    flex-direction: column;
  }
  .cont-form {
    width: 70%;
    margin-top: 0px;
  }
  .contact-img {
    height: 300px;
    object-fit: contain;
  }
}
@media screen and (max-width: 767px) {
  .contact-landpage {
    flex-direction: column;
  }
  .contact-form-header {
    width: 80%;
    text-align: left;
  }
  .contact-form-line {
    margin: 0px;
    width: 20%;
  }
  .cont-form {
    width: 90%;
    margin-bottom: 40px;
    margin-top: 0px;
  }
  .contact-img {
    height: 300px;
    object-fit: contain;
    margin-right: auto;
    margin-left: -40px;
    margin-top: 100px;
  }
  .contact-form-btn-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: red;
    margin-left: 0px;
  }
  .contact-form-btn {
    position: static;
  }
}
