* {
  padding: 0%;
  margin: 0%;
  outline: none;
  border: none;
  box-sizing: border-box;
  font-family: plus jakarta Text,poppins,sans-serif;
  text-decoration: none;
  list-style: none;
}
:root {
  --color-primary: rgb(8, 1, 34);
  --color-variant:rgb(13, 1, 54);
  --color-bold: rgb(100, 177, 244);
  --text-bright:white;
  --text-ash:#ddd;
  --font-large: 2rem;
  --font-medium: 1.1rem;
  --font-small: 1rem;
}
html {
  scroll-behavior: smooth;
}
body {
  position: relative;
  background-color: var(--color-primary);
}
a {
  font-size: 1.1rem;
  text-transform: capitalize;
  color: var(--text-ash);
}
h5 {
  font-size: var(--font-large);
  text-transform: capitalize;
  font-weight: 500;
  margin-bottom: 20px;
  color: var(--text-bright);
}
h6{
  color: var(--color-ash);
}
p {
  font-size: var(--font-medium);
  line-height: 28px;
  opacity: 0.8;
  color:var(--text-ash) ;
}

@media (max-width: 1024px) {
  h5 {
    font-size: 1.8rem;
  }
  
  p {
    font-size: 1rem;
  }
  .land-page {
    height: auto;
  }
  .swiper-container {
    height: auto;
  }
  .land-text {
    width: 60%;
  }
  .slide-img-container {
    width: 40%;
  }
}
@media screen and (max-width: 767px) {
  nav {
    display: none;
  }
  ul {
    flex-direction: column;
  }
  h5{
    font-size: 1.5rem;
  }
  .menu-container{
    height: 40px !important;
    width: 40px !important;
  }
  .close-sideber-icon svg{
    font-size: 1rem;
  }
  .show-sidebar{
    width: 60% !important;
  }
}
