.review-card {
  width: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 450px;
}
.review-section {
  width: 100%;
  margin-top: 50px;
  cursor: pointer;
}
.review-card-container {
  width: 80%;
}
.review-header {
  margin: auto;
  width: 80%;
}
.avatar {
  width: 100px;
  height: 100px;
  border-radius: 50px;
  object-fit: cover;
}
.quote {
  font-size: 4rem;
}
.client-name {
  margin-top: 20px;
  text-transform: capitalize;
  font-size: 0.9rem;
}
.review-text {
  width: 50%;
  word-wrap: break-word;
  text-align: center;
  margin-top: 30px;
}
@media screen and (max-width: 767px) {
  .review-header {
    width: fit-content;
  }
  .review-text {
    width: 95%;
    margin: auto;
  }
  .review-card{
    background:var(--color-variant);
    margin: auto;
    margin-top: 15px;
    width: 100%;
    padding-top: 15px;
  }
  .review-card-container{
    align-self: center;
    margin:0px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .review-section{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0px;
  }
}
