.dashboard-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: aliceblue;
  padding: 10px;
}
.dash-header p {
  font-size: 1.2rem;
  text-transform: capitalize;
  font-weight: 500;
}
.welcome {
  color: var(--color-bold);
  text-transform: capitalize;
  font-size: 1.5rem;
}
.side-bar {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  position: sticky;
  top: 0%;
  left: 0%;
  width: 20%;
  height: 100vh;
  align-self: flex-start;
  border-radius: 20px 0px 0px 20px;
  background-color: white;
}
.dash-body {
  width: 80%;
  height: auto;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0px 20px 0px 0px;
  position: relative;
  flex-direction: column;
  /* margin-left: 20px; */
}
.dash-header {
  position: absolute;
  top: 0;
  height: 10vh;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 95%;
  padding: 0px 20px 0px 40px;
  border-radius: 10px 10px 0px 0px;
  border-bottom: 1px solid rgb(240, 240, 240);
}
.dash-logo-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: 15%;
  width: 100%;
  padding-left: 30px;
  border-radius: 20px 0px 0px 0px;
  background-color: rgba(236, 236, 236, 0.683);
}
.dash-list-container:hover {
  border-right: 4px solid var(--color-bold);
  background-color: rgb(252, 252, 252);
}
.dash-list-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: 10px;
  padding: 10px 10px 10px 30px;
  transition: 0.1s ease all;
  border-right: 4px solid white;
}
.dash-list-container:hover .dash-link {
  color: black;
}
.dash-link {
  width: 75%;
  color: rgba(81, 81, 81, 0.738);
  text-transform: capitalize;
}
.dash-icon {
  font-size: 2.3rem;
  font-weight: 400;
  color: var(--color-bold);
}
.dash-chart {
  width: 95%;
  height: 400px;
  background-color: white;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.dash-overview-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 90px 0px 30px 0px;
}
.dash-overview-card {
  width: 240px;
  height: 150px;
  border-radius: 10px;
  margin: 10px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: white;
}
.dash-overview-card {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  padding: 10px 20px 10px 20px;
}
.amount-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.amount-container p {
  font-size: 2rem;
  font-weight: 500;
  flex-wrap: wrap;
  color: var(--color-bold);
}
.amount-container small {
  font-size: small;
  display: block;
  margin-bottom: 30px;
  text-transform: capitalize;
  color: rgb(124, 124, 124);
  font-size: 1.1rem;
}
.notification-icon {
  font-size: 2rem;
  cursor: pointer;
}
.money-icon {
  font-size: 2.8rem;
  color: var(--color-bold);
  padding: 5px;
}
.money-line {
  width: 60px;
  height: 2px;
  background-color: var(--color-bold);
}

.green {
  color: rgb(2, 178, 2);
}
.purple {
  color: purple;
}
.gold {
  color: goldenrod;
}
.chart-pic {
  height: 400px;
  width: 400px;
  object-fit: contain;
}
.chart-text-container {
  width: 400px;
}
.dash-btn-container {
  width: 100%;
  margin-top: 30px;
}

.dash-btn {
  width: 100%;
  padding: 15px 0px 15px 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--color-bold);
  color: white;
  font-size: 1.2rem;
  border-radius: 5px;
  text-transform: capitalize;
}
.fund-form-container {
  width: 95%;
  padding: 100px 20px 100px 20px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: white;
}
.fund-form {
  width: 50%;
  padding: 50px 20px 50px 20px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}
.payment-method-container {
  padding: 30px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  flex-wrap: wrap;
}
.payment-method-card {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  width: 45%;
  border-radius: 10px;
  box-shadow: 0px 0px 5px #ddd;
  transition: 0.03s ease all;
}
.payment-method-card:hover {
  cursor: pointer;
  border: 1px solid var(--color-bold);
}
.pay {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.pay label {
  width: 95%;
  margin: auto;
}
.pay input {
  width: 95%;
  margin: auto;
  text-indent: 10px;
}
.payment-method-card p {
  color: black;
}
.payment-icon {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.span-2 {
  width: 95%;
  margin-top: 20px;
}
.span-2 .payment-icon {
  width: 40%;
  height: 40%;
  object-fit: contain;
}
.investment-card-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: aliceblue;
}
.form-header {
  position: absolute;
  top: 0;
  padding: 30px 0px 0px 0px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-items: center;
  flex-direction: column;
  border-radius: 10px 10px 0px 0px;
  background-color: aliceblue;
}
.form-header h5 {
  color: var(--color-bold);
}
.submit-btn {
  width: 95%;
  border-radius: 10px;
  padding: 15px 0px 15px 0px;
  background-color: var(--color-bold);
  color: white;
  text-transform: capitalize;
  font-size: 1.1rem;
  cursor: pointer;
}
.submit-btn:hover {
  background-color: rgb(81, 170, 248);
}
.submit-btn-container {
  width: 100%;
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0px 0px 0px;
}
.bitcoin-form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  padding: 20px 0px 20px 0px;
}
.qr-code {
  margin: auto;
}
.bitcoin-wallet-address-container h6 {
  font-weight: 400;
  font-size: 1.2rem;
  margin-bottom: 20px;
}
.wallet-address {
  margin: 20px 0px 20px 0px;
}
.bitcoin-wallet-address-container {
  width: 80%;
  padding: 10px;
  word-break: break-all;
  word-wrap: break-word;
}
.invest-form-container {
  position: fixed;
  top: 0%;
  left: 0%;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.55);
  z-index: 3;
}
.invest-form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 400px;
  background-color: white;
  height: 500px;
  border-radius: 5px;
}
.invest-form input {
  width: 90%;
  /* height: 50px; */
  font-size: 1rem;
  text-transform: capitalize;
  padding: 5px;
  border: 1px solid var(--color-bold);
  margin-top: 20px;
  padding: 20px 10px 20px 10px;
  border-radius: 10px;
}
.note {
  color: black;
  font-weight: 500;
  text-transform: capitalize;
  font-size: 1.2rem;
}
.close-btn {
  cursor: pointer;
  position: absolute;
  top: 10%;
  right: 10%;
  background-color: white;
  font-size: 2rem;
  font-weight: bold;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  padding: 7px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.invest-submit-btn {
  cursor: pointer;
  background-color: var(--color-bold);
  color: white;
  text-transform: capitalize;
  margin-top: 50px;
}
.invest-form-text {
  margin-bottom: 50px;
  text-transform: capitalize;
  font-size: 2rem;
  text-align: left;
  width: 90%;
}
.menu-container {
  background-color: white;
  height: 70px;
  border-radius: 50%;
  width: 70px;
  position: fixed;
  top: 2%;
  left: 0%;
  z-index: 20;
  box-shadow: 0px 0px 10px #ddd;
  display: none;
}
.toggler {
  background-color: white;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.shift {
  margin-left: 85%;
}

.close-sideber-icon {
  height: 80%;
  width: 80%;
  color: white;
}
.note-container {
  width: 90%;
}
.dash-invest-container {
  flex-wrap: wrap;
}
.dash-invest-container .invest-card {
  margin-right: auto;
  box-shadow: 0px 0px 10px #ddd;
}
.dash-invest-container .invest-card:last-of-type {
  margin-left: -340px;
}
.small-container {
  display: block;
}
.bitcoin-container {
  width: 100px;
}
.note-container {
  margin-top: 50px;
  width: 90;
}
.note-container p {
  word-break: break-all;
}
.dash-info-container {
  margin: 20px 0px 40px 0px;
  background-color: white;
  padding: 20px 10px;
  width: 95%;
  border-radius: 20px;
}
.dash-info-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  text-transform: capitalize;
  padding: 0px 10px 0px 10px;
}
.withdraw-meth-cont p {
  margin-bottom: 20px;
  color: rgb(99, 99, 99);
}
.withdraw-meth-cont span {
  color: var(--color-bold);
}
.amount-withdaw-cont p {
  color: rgb(99, 99, 99);
  margin-bottom: 10px;
}
.amount-withdaw-cont span {
  color: var(--color-bold);
  margin-top: 20px;
  font-size: 2rem;
}
.pending-btn {
  padding: 5px 30px 5px 30px;
  background-color: rgb(243, 186, 43);
  border-radius: 4rem;
  color: white;
}
.pending-btn-container p {
  margin-bottom: 20px;
  color: rgb(99, 99, 99);
}
.fund-form{
  height: 125vh;
  min-height: auto !important;
  width: 600px !important;
}
.pay{
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.fund-form-container{
  height:auto !important;
  margin: 50px 0px;
}
.wallet-address-container{
  width: 95%;
  border-radius: 5px;
  border: 2px solid #ddd;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  margin: 10px;
}
.wallet-address-container textarea{
  color: rgb(38, 38, 38);
  width: 80%;
  font-size: .85rem;
  padding: 18px 0px 5px 0px;
}
.invest-form-text p{
  color: rgb(34, 33, 33);
}
.wallet-address-container span{
  width: 20%;
  cursor: pointer;
  background:var(--color-bold);
  color:var(--text-bright);
  padding: 10px 20px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.wallet-details-container{
  padding: 10px 0px ;
  width: 95%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.wallet-details-container span{
  margin-right: 20px;
  padding: 10px 25px;
  border-radius: 5px;
  color: var(--color-primary);
  background-color: var(--text-ash);
}
.dash-chart{
  height:auto;
}
.dash-invest-container{
  align-items: center;
  justify-content: space-around;
  width: 100%;
}
.withdraw-form{
  height: 500px;
}
@media (max-width: 1350px) and (min-width: 768px) {
  .menu-container {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  .dash-chart {
    padding: 10px 20px 10px 10px;
  }
  .close-sideber-icon {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .side-bar {
    width: 0px;
    overflow-x: hidden;
  }
  .show-sidebar {
    display: 80%;
    z-index: 4;
    position: fixed;
  }
  .dash-overview-container {
    flex-wrap: wrap;
    width: 100%;
  }
  .dash-overview-card {
    width: 40%;
  }
  .dash-body {
    width: 100%;
  }
  .dash-invest-container .invest-card:last-of-type {
    margin-left: 0px;
  }
  .show-sidebar {
    width: 75%;
    z-index: 400;
    overflow-x: auto;
    position: fixed;
    top: 0;
    left: 0;
    transition: all 0.3s ease-in-out;
  }
}

@media screen and (max-width: 767px) {
  .dash-invest-container .invest-card:last-of-type {
    margin-left: 0px;
  }
  .dash-header {
    padding-left: 60px;
  }
  .invest-card {
    align-self: center;
    margin-top: 120px;
  }
  .dash-invest-container {
    height: auto;
  }
  .menu-container {
    transition: all 0.3s ease-in-out;
    display: flex;
  }
  .welcome {
    font-size: 1.2rem;
  }
  .side-bar {
    width: 0px;
    overflow-x: hidden;
  }
  .close-sideber-icon {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: white;
    font-size: 1.5rem;
  }
  .show-sidebar {
    width: 75%;
    z-index: 400;
    overflow-x: auto;
    position: fixed;
    top: 0;
    left: 0;
    transition: all 0.3s ease-in-out;
  }
  .dash-overview-container {
    flex-wrap: wrap;
    width: 100%;
  }
  .dash-overview-card {
    width: 45%;
  }
  .dash-body {
    width: 100%;
  }
  .dash-chart {
    flex-direction: column;
    height: auto;
  }
  .amount-container p {
    font-size: 1.5rem;
  }
  .fund-form {
    width: 100%;
  }
  .home-sidebar{
    box-shadow: none;
  }
  .menu-container{
    top: 30px;
    box-shadow: none;
    left: 10px;
  }
  .wallet-details-container span{
    font-size: .8rem;
    padding: 10px 15px;
  }
  .wallet-address-container span{
    font-size: .9rem;
    padding: 10px;
  }
  .wallet-address-container textarea{
    font-size: .8rem;
    padding: 10px;
  }
  .dash-invest-container{
    padding-right: 0px;
    margin-right: 0px;
  }
}
@media screen and (max-width: 500px) {
  .dash-info-card {
    width: 100%;
    flex-wrap: wrap;
  }
  .amount-withdaw-cont {
    width: 50%;
    height: 150px;
  }
  .withdraw-meth-cont {
    width: 50%;
    height: 150px;
  }
  .pending-btn-container {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: flex-start;
    padding: 0px 30px 0px 0px;
    justify-content: space-between;
    flex-direction: column;
  }
  .dash-info-container {
    display: flex;
    flex-wrap: wrap;
  }
  .dash-overview-card {
    width: 100%;
  }
  .chart-text-container {
    width: 100%;
  }
  .chart-pic {
    width: 100%;
  }
  .dash-chart {
    width: 100%;
    padding: 0px 10px 0px 10px;
    height: auto;
  }
  .fund-form {
    width: 100%;
  }
  .dash-list-container svg{
    font-size: 1.5rem;
  }
  .dash-list-container a{
    font-size: 1rem;
  }
  .dash-list-container{
    margin-top: 0px;
  }
}
