.credit-form-container{
    width: 95%;
    padding: 20px 10px;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px;
}
.credit-form{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.my-input{
    height: 50px;
    text-indent: 15px;
    border: 1px solid #ddd;
    border-radius: 10px;
    width: 40%;
}
.my-input::placeholder{
    text-transform: capitalize;
    font-size: 1rem;
}
.credi-user{
    padding: 10px 30px;
    border-radius: 10px;
    background-color: var(--color-primary);
    color: white;
    text-transform: capitalize;
    cursor: pointer;
}
.admin-user-display{
    width: 95%;
    background-color: white;
    padding: 20px 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
}
.user-details-container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;

}
.user-email{
    width: 40%;
}
.user-name{
    width: 30%;
}
.user-password{
    white-space: 20%;
}
@media screen  and (max-width:740px){
    .credit-form{
        flex-direction: column;
    }
    .my-input{
        width: 100%;
    }
    .credi-user{
        width: 100%;
    }
    .credit-form input{
        margin-top: 30px;
    }
    .user-details-container{
        flex-direction: column;
    }
    .user-email{
        width: 100%;
    }
    .user-name{
        width: 100%;
    }
    .user-password{
        width: 100%;
    }
}