.overview-section{
  width: 90%;
  padding: 20px 10px 20px 10px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: relative;
  margin: auto;
  margin-top: 20px;
}
.overview-header {
  padding: 20px;
  text-align: center;
}
.overview-header h5{
  color: var(--text-bright);
}
.overview-header p {
  width: 60%;
  margin: auto;
}
.overview-card {
  width: 400px;
  height: 330px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}
.overview-icon {
  font-size: 3.5rem;
  color: var(--color-bold);
  margin-right: auto;
  margin-left: 10px;
}
.overview-text-container {
  padding: 0px 10px 20px 10px;
}
.overview-text-container p {
  font-size: 1.1rem;
}
.overview-text-container h6 {
  font-size: 1.5rem;
  margin-bottom: 25px;
  font-weight: 500;
  text-transform: capitalize;
  color: var(--text-bright);
}
.line {
  background-color: var(--color-bold);
  display: block;
  width: 150px;
  height: 3px;
  margin-right: auto;
  margin-top: 5px;
}
.prize-chart-container {
  width: 100%;
  background-color: var(--color-variant);
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0px 20px 30px 20px;
  height: 70vh;
}
.prize-card {
  width: 50%;
}
.live-chart-header {
  width: fit-content;
  margin-left: 50px;
  text-transform: capitalize;
  margin-bottom: 20px;
}
@media (max-width: 990px) and (min-width: 768px) {
  .overview-section {
    flex-wrap: wrap;
    height: auto;
    width: 100%;
    background-color: white;
  }
  .overview-card {
    width: 50%;
  }
}
@media screen and (max-width: 767px) {
  .overview-section{
    width: 100%;
  }
  .overview-header{
    padding: 20px 0px;
  }
  .overview-header p{
    width: 95%;
    text-align: left;
  }
  .overview-text-container {
    width: 90%;
  }
  .overview-header p {
    width: 100%;
  }
  .prize-chart-container{
    padding: 40px 10px;
  }
  .prize-card {
    width: 80%;
  }
  .prize-chart-img {
    width: 80%;
  }
  .overview-section {
    flex-direction: column;
    width: 95%;
    margin: auto;
    padding: 20px 10px;
  }
  .prize-chart-container {
    flex-direction: column;
    height: auto;
  }
  .overview-card{
    background-color: var(--color-variant);
    max-width: 100%;
    margin-bottom: 15px;
  }
  .overview-text-container{
    width: 100%;
  }
  .overview-header{
    max-width: 90%;
    margin: auto;
    text-align: left;
  }
}
